.galerij_element {
    background: #A9BDA1;
    width: 433px;
    height: 500px;
    margin: 50px;
}

.galerij_afbeelding {
    width: 433px;
    height: 350px;
    object-fit: cover;
}



.galerij_page_top {
    display: grid;
    grid-template-columns: 50% 50%;
}

@media (min-width:1000px){
.galerij_page {
    width: 94vw;
}

.galerij_photo {
    margin: 0.2vw;
    height: 20.6vw;
}
.galerij_list {
    display: grid;
    grid-template-columns: 30vw 30vw 30vw;
    margin-left: 100px;
    margin-right: 100px;
    text-align: center;
}
}

@media (max-width:1000px){
.galerij_page {
    width: 94vw;
}

.galerij_photo {
    margin: 0.2vw;
    height: 62vw;
}
.galerij_list {
    display: grid;
    text-align: center;
}
}

/* Style the Image Used to Trigger the Modal */
#myImg {
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
}

#myImg:hover {opacity: 0.7;}

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  padding-top: 100px; /* Location of the box */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.9); /* Black w/ opacity */
}

/* Modal Content (Image) */
.modal-content {
  margin: auto;
  display: block;
  width: 80%;
    height: 80%;
    object-fit: contain;
}


/* Add Animation - Zoom in the Modal */
.modal-content, #caption {
  animation-name: zoom;
  animation-duration: 0.6s;
}

@keyframes zoom {
  from {transform:scale(0)}
  to {transform:scale(1)}
}

/* The Close Button */
.close {
  position: absolute;
  top: 15px;
  right: 35px;
  color: #f1f1f1;
  font-size: 40px;
  font-weight: bold;
  transition: 0.3s;
}

.close:hover,
.close:focus {
  color: #bbb;
  text-decoration: none;
  cursor: pointer;
}

/* 100% Image Width on Smaller Screens */
@media only screen and (max-width: 700px){
  .modal-content {
    width: 100%;
  }
}